.v3.has-wallpaper {
    .plugins-pane {
        background-color: #1d1f24c9;
    }
}
.plugins-pane.showing {

    .collapse-and-label {
        flex-direction: column;
        .plugins-label {
            margin-top: 10px;
            margin-left: 0px;
        }
    }
}

.plugins-pane {
    border-top: 1px solid var(--dividerColor);
    padding: 5px;
    position: fixed;
    display: flex;

    .plugins-container {
        padding-left: 10px;
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        height: 100%;
        flex: 1;
    }


    background-color: var(--editor-bottom);
    z-index: 100000000;

    .collapse-and-label {
        display: flex;
        flex-direction: row;
    }

    .plugins-label {
        img {
            height: 18px;
        }
        margin-left: 5px;
        /* color: black; */
    }

    .z-object.custom.custom-subpatch-container {
        .inner-custom {
            overflow: visible;
        }
    }
    .z-object {
        z-index: unset !important;
        position: relative !important;
        top: unset !important;
        left: unset !important;
        box-shadow: unset !important;
        overflow: visible;
        .inlets, .outlets {
            display: none;
        }
    }
}
