.pad-tabs {
    display: flex;
    
    .new-pad-btn {
        padding-left: 10px;
        padding-right: 10px;
        background-color: var(--sidebar-btn);
        margin-left: 5px;
    }

    .pad-option.selected, .pad-option.selected:hover {
        //background-color: #161616;
        border-bottom: 2px solid var(--antiColor);
        color: #fda80c;
        font-style: italic;
    }

    .pad-option:hover {
        background-color: #3131315a;
    }
    .pad-option.editing {
        padding-right: 0px;
        padding-left: 0px;
    }
    .pad-option {
        cursor: pointer;
        padding-left: 10px;
        font-style: normal;
        padding-right: 10px;
        padding-bottom: 5px;
        color: var(--antiColor);
        background-color: var(--sidebar-btn);
        border-right: 1px solid var(--dividerColor);
        height: 30px;
        font-size: 10pt;
        line-height: 30px;
        border-top-right-radius: 1px;
        border-top-left-radius: 1px;

        .pad-name-input {
            background-color: black;
            color: white;
        }
    }
}
