.v3 {
    .search-background.hide {
        display: none;
    }

    .search-background {
        position: absolute;
        display: flex;
        z-index: 1000000000000;
        width: 100%;
        height: 100%;
        background-color: #0e0e0e59;
        align-items: flex-start;
        pointer-events: none;
    }

    .search-window {
        pointer-events: all;
        margin-top: 80px;
        margin-left: auto;
        margin-right: auto;
        z-index: 10000000;
        color: black;
        background-color: white;
        padding: 15px;
        align-items: flex-start;
        width: 470px;
        //height: 400px;
        top: 80px;
        left: 40%;
        border-radius: 15px;
        background-color: #2b2b2bb5;
        backdrop-filter: blur(4px);

        .search-icon {
            position: absolute;
            top: 25px;
            left: 15px;
            width: 15px;
            height: 15px;
        }

        .quick-actions {
            display: flex;
            margin-top: 10px;
            margin-left: 0px;
            width: unset;

            .action {
                color: black;
                background-color: white;
                border-radius: 12px;
                margin-right: 10px;
                padding: 5px;
            }
        }

        input.search-text {
            width: 100%;
            background-color: transparent;
            color: white;
            padding: 5px;
            padding-left: 25px;
            padding-bottom: 10px;
            /* border-radius: 10px; */
            border: none !important;
            outline: none !important;
            border-bottom: 1px solid gray !important;

        }


        .search-results.empty {
            min-height: unset;
            margin-top: 0px;
            margin-bottom: 0px;
        }
        
        .search-results {
            color: #ababab;
            font-size: 10pt;
            max-height: 500px;
            overflow-y: scroll;

            //.results-type {
            //    position: relative;

                .type-name {
                    background-color: black;
                    border-radius: 1px;
                    padding: 5px;
                    font-size: 10pt;
                    background-color: #00000040;
                    border-bottom: 1px solid #ffffff29;
                }

                .result.cursor {
                    background-color: white;
                    color: black;

                    img {
                        filter: invert(1);
                    }
                }

                .result {
                    cursor: pointer;
                    padding: 4px;

                    img {
                        width: 25px;
                        margin-right: 10px;
                    }

                    .zobject-preview {
                        display: flex;

                        .custom-type {
                            background-color: #00ffc4;
                            color: black;
                            margin-left: 10px;
                            padding-left: 3px;
                            padding-right: 3px;
                            margin-top: 3px;
                            height: 15px;
                            line-height: 15px;
                            font-size: 8pt;
                            border-radius: 3px;
                        }

                        .object-icon {
                            height: 15px;
                            width: 15px;
                            margin-top: 3px;
                            margin-right: 10px;
                        }
                        .tree-part.spatch {
                            background-color: white;
                            color: black;
                            padding-left: 3px;
                            padding-right: 3px;
                            margin-top: 3px;
                            height: 15px;
                            line-height: 15px;
                            font-size: 8pt;
                            border-radius: 3px;
                        }
                        .tree-part.main-part {
                            margin-right: 8px;
                        }
                        .tree-part {
                            margin-right: 6px;
                        }
                    }
                }
            }
        //}

        .new-selector.has-back {
            .entire-search-container {
                display: flex;
            }
        }

        .new-selector {
            color: white;
            width: 99%;
            overflow-y: hidden;

            .day-results, .source-results, .sound-grid {
                padding-top: 0px;
            }

            .entire-search-container {
                display: none;
                position: unset;
                width: 90%;
                height: 35px;

                .search-container, .filter-options {
                    display: none;
                }
            }

        }
    }
}
