.v3 {
.samples-to-save {
    box-shadow: #3333334f 0px 54px 55px, #3b3b3b1f 0px -12px 30px, #404040cf 0px 4px 6px, #46464666 0px 12px 13px, #4949492e 0px -3px 5px;
    position: absolute;
    width: 400px;
    max-height: 450px;
    z-index: 1000000000000000;
    border-radius: 15px;
    padding: 10px;
    background-color: #d2cdcdeb;
    background-image: linear-gradient(45deg, #8b9cc1, transparent);

    ::-webkit-scrollbar-thumb {
        background-color: #bab6b6 !important;
    }

    .samples-list {
        max-height: 300px;
        overflow-y: scroll;
    }

    .confirmation {
        position: absolute;
        width: 100%;
        height: 400px;
        top: 0px;
        left: 0px;
        background-color: black;
        z-index: 100000000000000000000;
        border-radius: 15px;

        .confirmation-text {
            padding: 10px;
        }

        .buttons {

            .save-btn.cancel {
                background-color: red;
                color: black;
                margin-left: 10px;
            }
        }

    }

    .tags-editor {
        margin-top: 5px;

        .tags-container {
            display: flex;
            flex-wrap: warp;

            .tag-option {
                background-color: black;
                margin: 3px;
                padding: 3px;
                line-height: 20px;
                height: 25px;
            }
        }

        .tag-input-container {
            display: flex;

            .add-btn {
                height: 25px;
                width: 25px;
                line-height: 25px;
                text-align: center;
                font-size: 14pt;
                background-color: white;
                color: black;
                border-top-right-radius: 5px;
                cursor: pointer;
            }
            input.tag-input {
                height: 25px;
                line-height: 25px;
                width: 95%;
                background-color: black;
                color: white;
            }
        }
    }
    .buttons {
        margin-top: 5px;
        padding-top: 5px;
        display: flex;
        position: relative;

        .show-login-btn {
            color: #00f496;
            text-decoration: underline;
            margin-right: 5px;
        }
        
        .btn-primary, .btn-primary:hover, .btn-primary:active, .btn-primary:visited, .btn-primary:focus {
            background-color: #f5f6f7 !important;
            color: black !important;
            border-color: #082435 !important;
        }

        .save-btn {
            color: black;
            margin-left: 10px;
            padding: 5px;
            background-color: white;
            border-radius: 15px;
            cursor: pointer;
        }
    }

    .sample-to-save.selected {
        background-color: white;
        color: black;
    }
    .sample-to-save {
        display: flex;
        .sts-tag {
            margin-left: 5px;
            background-color: black;
            padding-left: 4px;
            padding-right: 4px;
            border-radius: 5px;
            color: white;
        }

        max-width: 380px;
        overflow: hidden;
        padding: 5px;
        line-height: 20px;
        font-size: 9pt;
        background-color: #00000052;
    }
}
}
