.zobject-preview {
    position: relative;
    overflow: visible;
    .zobject-command {
        position: absolute;
        right: 0px;
        top: -3px;

        .collapse-btn {
            background-color: #0000001c;
            width: 25px;
            height: 25px;
            cursor: pointer;
            padding: 3px;
            padding-top: 5px;
            border-radius: 20px;

            .arrow-up {
                border-left: 8px solid transparent;
                border-right: 8px solid transparent;
                border-bottom: 15px solid #7e869e;
            }
            .arrow-down {
                width: 0;
                height: 0;
                /* margin-top: 5px; */
                margin-left: 2px;
                border-left: 8px solid transparent;
                border-right: 8px solid transparent;
                border-top: 15px solid #7e869e;
            }
        }

        .command-menu {
            position: absolute;
            right: 0px;
            top: 25px;
            padding: 3px;
            background-color: #7a7f8ba3;
            backdrop-filter: blur(4px);
            width: 120px;
            min-height: 50px;
            border-radius: 5px;
            color: white;
            
            .command-option:hover {
                background-color: white;
                color: black;
            }
            .command-option {
                cursor: pointer;
                padding: 5px;
            }
        }
    }
}
